import React, { useState } from 'react'
import JSBI from 'jsbi'
import { useWeb3React } from '@web3-react/core'
import { useUnlimitedContract, useWhitelistContract } from 'hooks/useContract'
import useDonateCanDonate from 'views/Imos/hooks/whitelist/useDonateCanDonate'
import { Button, Input } from 'packages/uikit/src'
import { formatUnits } from 'ethers/lib/utils'
import { useTranslation } from 'contexts/Localization'
import { Ifo, PoolIds } from 'config/constants/types'
import { WalletIfoData, PublicIfoData } from 'views/Imos/types'
import ConnectWalletButton from 'components/ConnectWalletButton'
import ContributeButton from './ContributeButton'
import ClaimButton from './ClaimButton'
import { SkeletonCardActions } from './Skeletons'
import RefundButton from './RefundButton'

interface Props {
  poolId: PoolIds
  ifo: Ifo
  publicIfoData: PublicIfoData
  walletIfoData: WalletIfoData
  hasProfile: boolean
  isLoading: boolean
}

const IfoCardActions: React.FC<any> = ({ poolId, ifo, walletIfoData, isLoading, whitelist, phase, userInfo }) => {
  const { account } = useWeb3React()
  const [value, setValue] = useState('')
  const { t } = useTranslation()
  // const canDonate = whitelist
  //   ? JSBI.greaterThan(wlCanDonate || JSBI.BigInt(0), JSBI.BigInt(0))
  //   : JSBI.greaterThan(unCanDonate || JSBI.BigInt(0), JSBI.BigInt(0))

  const canClaim =
    Number(formatUnits(userInfo.totalPayAmount || 0, 18)) > 0 &&
    Number(formatUnits(userInfo.canClaimAmount || 0, 18)) > 0

  const canRefund = !userInfo.refunded && Number(formatUnits(userInfo.totalPayAmount || 0, 18)) > 0

  if (isLoading) {
    return <SkeletonCardActions />
  }

  if (!account) {
    return <ConnectWalletButton width="100%" />
  }

  return (
    <>
      {whitelist && phase !== 4 ? (
        <>
          <Input
            onChange={(e) => setValue(e.target.value)}
            placeholder={t('Enter Amount, MAX 400')}
            type="number"
            value={value}
            max={400}
            min={0}
            style={{ marginBottom: '24px' }}
          />
          <ContributeButton ifo={ifo} value={value} whitelist={whitelist} />
        </>
      ) : phase !== 4 ? (
        <>
          <Input
            onChange={(e) => setValue(e.target.value)}
            placeholder={t('Please Input ETH Number')}
            type="number"
            value={value}
            max={1}
            min={0}
            style={{ marginBottom: '24px' }}
          />
          <ContributeButton ifo={ifo} value={value} whitelist={whitelist} />
        </>
      ) : null}

      {/* {!whitelist && canRefund && phase === 4 && <RefundButton ifo={ifo} />} */}

      {(phase === 4 && canClaim && (
        <ClaimButton
          ifo={ifo}
          whitelist={whitelist}
          poolId={poolId}
          ifoVersion={ifo.version}
          walletIfoData={walletIfoData}
        />
      )) ||
        null}
    </>
  )
}

export default IfoCardActions
