import React, { useState } from 'react'
import { AutoRenewIcon, Button } from '@pancakeswap/uikit'
import { PoolIds } from 'config/constants/types'
import { WalletIfoData } from 'views/Imos/types'
import { useTranslation } from 'contexts/Localization'
import useToast from 'hooks/useToast'
import { useIMODOnateLP, useIMODonateCore, useUnlimitedContract, useWhitelistContract } from 'hooks/useContract'

interface Props {
  poolId: PoolIds
  ifoVersion: number
  walletIfoData: WalletIfoData
}

const ClaimButton: React.FC<any> = ({ poolId, ifoVersion, walletIfoData, whitelist, ifo }) => {
  const { t } = useTranslation()
  const { toastError, toastSuccess } = useToast()
  const [loading, setLoading] = useState(false)
  const WLC = useWhitelistContract()
  const UNC = useUnlimitedContract()

  const mdaoLp = useIMODOnateLP()
  const mdaoUN = useIMODonateCore()
  const _c = ifo.id === 'hit' ? (whitelist ? WLC : UNC) : whitelist ? mdaoLp : mdaoUN
  const handleClaim = async () => {
    try {
      setLoading(true)
      console.log(_c, ifo.poolUnlimited.claimFunc, 'kkk')
      const tx = await _c[ifo.poolUnlimited.claimFunc]()
      await tx.wait()
      toastSuccess(t('Success!'), t('You have successfully claimed your rewards.'))
      setTimeout(() => {
        window.location.reload()
      }, 2000)
    } catch (error) {
      toastError(t('Error'), t('Please try again. Confirm the transaction and make sure you are paying enough gas!'))
      console.error(error)
    } finally {
      setLoading(false)
    }
  }

  return (
    <Button variant="danger" onClick={handleClaim} width="100%" isLoading={loading}>
      领取 {ifo.token.symbol}
    </Button>
  )
}

export default ClaimButton
