import React, { useCallback, useMemo, useState } from 'react'
import BigNumber from 'bignumber.js'
import { useCallWithGasPrice } from 'hooks/useCallWithGasPrice'
import { getBep20Contract, getUnlimitedContract, getWhiteListContract } from 'utils/contractHelpers'
import { Button, useModal } from '@pancakeswap/uikit'
// import { getBalanceNumber } from 'utils/formatBalance'
import { Ifo, PoolIds } from 'config/constants/types'
import { WalletIfoData, PublicIfoData } from 'views/Imos/types'
import { useTranslation } from 'contexts/Localization'
// import useTokenBalance from 'hooks/useTokenBalance'
// import { getAddress } from 'utils/addressHelpers'
import useToast from 'hooks/useToast'
import { useWeb3React } from '@web3-react/core'
import {
  useERC20,
  useIMODOnateLP,
  useIMODonateCore,
  useUnlimitedContract,
  useWhitelistContract,
} from 'hooks/useContract'
import { parseUnits } from 'ethers/lib/utils'
import { ethers } from 'ethers'
import { ChainId } from 'packages/sdk/src'
import useActiveWeb3React from 'hooks/useActiveWeb3React'
// import ContributeModal from './ContributeModal'
// import GetLpModal from './GetLpModal'

interface Props {
  poolId: PoolIds
  ifo: Ifo
  publicIfoData: PublicIfoData
  walletIfoData: WalletIfoData
}

const baseETHSinglePrice = 30000000000000
const ContributeButton: React.FC<any> = ({ whitelist, value, ifo }) => {
  const { t } = useTranslation()
  const [loading, setLoading] = useState(false)
  const [approved, setApproved] = useState(false)
  // const publicPoolCharacteristics = publicIfoData[poolId]
  // const userPoolCharacteristics = walletIfoData[poolId]
  // const { isPendingTx, amountTokenCommittedInLP } = userPoolCharacteristics
  // const { limitPerUserInLP } = publicPoolCharacteristics
  const { toastSuccess, toastError } = useToast()
  // const { balance: userCurrencyBalance } = useTokenBalance(getAddress(ifo.currency.address))
  const { callWithGasPrice } = useCallWithGasPrice()
  const wlC = useWhitelistContract()
  const unC = useUnlimitedContract()
  const mdaoLp = useIMODOnateLP()
  const mdaoUN = useIMODonateCore()
  const { library } = useActiveWeb3React()

  const lpContract = useMemo(() => {
    if (ifo.id !== 'hit' && whitelist) {
      return getBep20Contract(ifo.currency.address[ChainId.MAINNET], library.getSigner())
    }
    return null
  }, [ifo, library])
  const _c = ifo.id === 'hit' ? (whitelist ? wlC : unC) : whitelist ? mdaoLp : mdaoUN
  const methodsName = ifo.id === 'hit' ? (whitelist ? 'WLDonate' : 'ETHDonate') : whitelist ? 'LPDonate' : 'ETHDonate'
  const handleDonate = async () => {
    try {
      if (!value) return
      setLoading(true)
      console.log(parseUnits(value, 18).toString(),'kkkk')
      const tx = await callWithGasPrice(
        _c,
        methodsName,
        [whitelist ? value.toString() : parseUnits(value, 18).toString()],
        (!whitelist && {
          value: parseUnits(value, 18).toString(),
        }) || {
          value: (Number(value) * baseETHSinglePrice).toString(),
        },
      )
      const receipt = await tx.wait()
      if (receipt.status) {
        toastSuccess(t('ETH successfully transferred!'))
      }
      setTimeout(() => {
        window.location.reload()
      }, 2000)
    } catch (e) {
      toastError('Error')
      console.error(e)
    } finally {
      setLoading(false)
    }
  }
  const handleApprove = async () => {
    try {
      setLoading(true)
      const tx = await callWithGasPrice(lpContract, 'approve', [mdaoLp.address, ethers.constants.MaxUint256])
      const receipt = await tx.wait()
      if (receipt.status) {
        setApproved(true)
        toastSuccess(t('ETH successfully transferred!'))
      }
      setLoading(false)
    } catch (e) {
      toastError('Error')
      setLoading(false)
    }
  }

  // todo: change here
  const isDisabled = false
  return ifo.id === 'hit' ? (
    <Button variant="primary" isLoading={loading} onClick={handleDonate} width="100%" disabled={isDisabled}>
      {loading ? 'loading...' : isDisabled ? t('No Allowance') : ifo.id === 'hit' ? t('Commit ETH') : t('Commit LP')}
    </Button>
  ) : !whitelist || approved ? (
    <Button variant="primary" isLoading={loading} onClick={handleDonate} width="100%" disabled={isDisabled}>
      {loading
        ? 'loading...'
        : isDisabled
        ? t('No Allowance')
        : ifo.id === 'hit' || !whitelist
        ? t('Commit %token%', { token: 'ETH' })
        : t('Commit %token%', { token: 'LP' })}
    </Button>
  ) : (
    <Button variant="primary" isLoading={loading} onClick={handleApprove} width="100%" disabled={isDisabled}>
      {loading ? `${t('Approve')}...` : t('Approve')}
    </Button>
  )
}

export default ContributeButton
