import React from 'react'
import { ifosConfig } from 'config/constants'
import { useIMODOnateLP, useIMODonateCore, useUnlimitedContract, useWhitelistContract } from 'hooks/useContract'
import useGetPublicIfoV2Data from 'views/Imos/hooks/v2/useGetPublicIfoData'
import useGetWalletIfoV2Data from 'views/Imos/hooks/v2/useGetWalletIfoData'
import useDonatePhase from './hooks/whitelist/useDonatePhase'
import IfoFoldableCard from './components/IfoFoldableCard'
import IfoLayout from './components/IfoLayout'
import IfoSteps from './components/IfoSteps'
import IfoQuestions from './components/IfoQuestions'

/**
 * Note: currently there should be only 1 active IMO at a time
 */
const activeIfo = ifosConfig.find((ifo) => ifo.isActive)

const Ifo = () => {
  const publicIfoData = useGetPublicIfoV2Data(activeIfo)
  const walletIfoData = useGetWalletIfoV2Data(activeIfo)
  const WLC = useWhitelistContract()
  const UNC = useUnlimitedContract()

  const mdaoLp = useIMODOnateLP()
  const mdaoUN = useIMODonateCore()

  const whitelistPhase = useDonatePhase(activeIfo.id === 'hit' ? WLC : mdaoLp)
  const unlimitedPhase = useDonatePhase(activeIfo.id === 'hit' ? UNC : mdaoUN)

  
  return (
    <IfoLayout>
      <IfoFoldableCard
        wlPhase={whitelistPhase}
        unPhase={unlimitedPhase}
        ifo={activeIfo}
        publicIfoData={publicIfoData}
        walletIfoData={walletIfoData}
        isInitiallyVisible
        // hideLP
      />
      {/* <IfoSteps ifo={activeIfo} walletIfoData={walletIfoData} /> */}
      {/* <IfoQuestions /> */}
    </IfoLayout>
  )
}

export default Ifo
