import { useEffect, useState } from 'react'
import { useWeb3React } from '@web3-react/core'

// Retrieve IMO
const useDonatePhase = (contract): number => {
  const { account } = useWeb3React()
  const [allowance, setAllowance] = useState(0)
  useEffect(() => {
    const fetch = async () => {
      try {
        const res = await contract.nowPhase()
        setAllowance(res)
      } catch (e) {
        console.error(e)
      }
    }

    if (account) {
      fetch()
    }
  }, [account, contract])

  return allowance
}

export default useDonatePhase
