import { useEffect, useState } from 'react'
import { useWeb3React } from '@web3-react/core'
import { getWhiteListContract } from 'utils/contractHelpers'

// Retrieve IMO allowance
const useWhiteListAllowance = (contract): boolean => {
  const { account } = useWeb3React()
  const [allowance, setAllowance] = useState(false)
  useEffect(() => {
    const fetch = async () => {
      try {
        const res = await contract.isInWhiteList(account)
        setAllowance(res)
      } catch (e) {
        console.error(e)
      }
    }

    if (account) {
      fetch()
    }
  }, [account,contract])

  return allowance
}

export default useWhiteListAllowance
