import { useEffect, useState } from 'react'
import { useWeb3React } from '@web3-react/core'

// 2: core 3: wl
const useWhiteListPoolInfo = (contract, pid: number): boolean => {
  const { account } = useWeb3React()
  const [res, setRes] = useState(false)

  useEffect(() => {
    const fetch = async () => {
      try {
        const _res = await contract.poolInfos(pid)
        setRes(_res)
      } catch (e) {
        console.error(e)
      }
    }

    if (account) {
      fetch()
    }
  }, [account, pid, contract])

  return res
}

export default useWhiteListPoolInfo
