import React from 'react'
import useGetPublicIfoV2Data from 'views/Imos/hooks/v2/useGetPublicIfoData'
import useGetWalletIfoV2Data from 'views/Imos/hooks/v2/useGetWalletIfoData'
import { Ifo } from 'config/constants/types'
import { useUnlimitedContract, useWhitelistContract } from 'hooks/useContract'
import IfoFoldableCard from './IfoFoldableCard'
import useDonatePhase from '../hooks/whitelist/useDonatePhase'

interface Props {
  ifo: Ifo
  isInitiallyVisible: boolean
}

const IfoCardV2Data: React.FC<Props> = ({ ifo, isInitiallyVisible }) => {
  const publicIfoData = useGetPublicIfoV2Data(ifo)
  const walletIfoData = useGetWalletIfoV2Data(ifo)
  const WLC = useWhitelistContract()
  const UNC = useUnlimitedContract()

  const whitelistPhase = useDonatePhase(WLC)
  const unlimitedPhase = useDonatePhase(UNC)
  return (
    <IfoFoldableCard
      wlPhase={whitelistPhase}
      unPhase={unlimitedPhase}
      ifo={ifo}
      publicIfoData={publicIfoData}
      walletIfoData={walletIfoData}
      isInitiallyVisible={isInitiallyVisible}
    />
  )
}

export default IfoCardV2Data
