import React from 'react'
import styled from 'styled-components'
import { useTranslation } from 'contexts/Localization'
import { Card, CardBody, CardHeader, Text, useTooltip, HelpIcon, Flex } from '@pancakeswap/uikit'
import { Ifo, PoolIds } from 'config/constants/types'
import { useProfile } from 'state/profile/hooks'
import { PublicIfoData, WalletIfoData } from 'views/Imos/types'
import { EnableStatus } from '../types'
import IfoCardTokens from './IfoCardTokens'
import IfoCardActions from './IfoCardActions'
import IfoCardDetails from './IfoCardDetails'

interface IfoCardProps {
  poolId: PoolIds
  ifo: Ifo
  publicIfoData: PublicIfoData
  walletIfoData: WalletIfoData
  onApprove: () => Promise<any>
  enableStatus: EnableStatus
}

interface CardConfig {
  [key: string]: {
    title: string
    variant: 'blue' | 'violet'
    tooltip: string
  }
}



const StyledCardHeader = styled(CardHeader)`
  background-image: url('images/bg/card_banner.svg');
  background-size: 100%;
`

const SmallCard: React.FC<any> = ({
  poolId,
  ifo,
  publicIfoData,
  walletIfoData,
  onApprove,
  enableStatus,
  isInWhiteList,
  whitelist,
  poolInfo,
  phase,
  userInfo,
}) => {
  const { t } = useTranslation()
  const cardConfig: CardConfig = {
    [PoolIds.poolBasic]: {
      title: t('Whitelist Sale'),
      variant: 'blue',
      tooltip: 'Every person can only commit at most 400 amount of whitelist price at almost 20 usdt',
    },
    [PoolIds.poolUnlimited]: {
      title: t('Overraised Sale'),
      variant: 'violet',
      tooltip: 'At most 1 ETH you can commit per address',
    },
    [PoolIds.poolLp]: {
      title: 'LP Sale',
      variant: 'blue',
      tooltip: 'Every person can only commit at most 1700 LP',
    },
  }
  const config = cardConfig[poolId]
  const { hasProfile, isLoading: isProfileLoading } = useProfile()
  const { targetRef, tooltip, tooltipVisible } = useTooltip(t(config.tooltip), { placement: 'bottom' })
  // const isLoading = isProfileLoading || publicIfoData.status === 'idle'
  const isLoading = publicIfoData.status === 'idle'
  return (
    <>
      {tooltipVisible && tooltip}
      <Card>
        <StyledCardHeader>
          <Flex justifyContent="space-between" alignItems="center">
            <Text color="#fff" bold fontSize="20px">
              {t(config.title)}
            </Text>
            <div ref={targetRef}>
              <HelpIcon color="primary" />
            </div>
          </Flex>
        </StyledCardHeader>
        <CardBody>
          <IfoCardTokens
            poolInfo={poolInfo}
            poolId={poolId}
            ifo={ifo}
            publicIfoData={publicIfoData}
            walletIfoData={walletIfoData}
            hasProfile={hasProfile}
            isLoading={isLoading}
            phase={phase}
            onApprove={() => console.log('approve')}
            enableStatus={enableStatus}
            whitelist={whitelist}
            isInWhitelist={isInWhiteList}
            userInfo={userInfo}
          />
          <IfoCardActions
            poolId={poolId}
            ifo={ifo}
            publicIfoData={publicIfoData}
            walletIfoData={walletIfoData}
            hasProfile={hasProfile}
            isLoading={isLoading}
            whitelist={whitelist}
            phase={phase}
            userInfo={userInfo}
          />
          {/* <IfoCardDetails poolId={poolId} ifo={ifo} publicIfoData={publicIfoData} /> */}
        </CardBody>
      </Card>
    </>
  )
}

export default SmallCard
